@mixin xs_screen {
  @media (min-width: 321px) {
    @content;
  }
}

@mixin s_screen {
  @media (min-width: 576px) {
    @content;
  }
}

@mixin m_screen {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin l_screen {
  @media (min-width: 992px) {
    @content;
  }
}

@mixin xl_screen {
  @media (min-width: 1300px) {
    @content;
  }
}
